.player {
    width: 100%;
    height: 100%;
    background-color: white;
    animation: slide-in 400ms ease-out forwards;
    position: absolute;
    display: flex;
    flex-direction: column;
}

@keyframes slide-in {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0);
    }
}

.container {
    flex-grow: 1;
    padding: 5px;
}

.songInfoContainer {
    width: 100%;
    height: 100%;
    perspective: 600px;
    display: flex;
    position: relative;
}

.songInfoContainer img {
    padding: 10px 0 0 10px;
    width: 60%;
    height: 70%;
    transform: rotateY(45deg);
    transform-origin: left;
    -webkit-box-reflect: below 0 linear-gradient(to bottom, transparent, transparent, transparent, rgba(0, 0, 0, 0.4));
}

.songInfo {
    padding-top: 8%;
    transform: translate(-20%);
    text-transform: capitalize;
    width: 60%;
    height: 65%;
    font-size: 17px;
}

.songInfo p:last-child {
    text-transform: none;
}

.songInfoTitle {
    font-weight: 700;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-transform: capitalize;
}

.songInfoArtist,
.songInfoAlbumName,
.songInfoIndex {
    color: rgb(130, 130, 130);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}