.coverflow {
    width: 100%;
    height: 100%;
    background-color: white;
    animation: slide-in 400ms ease-out forwards;
    position: absolute;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

@keyframes slide-in {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0);
    }
}

.container {
    flex-grow: 1;
    position: relative;
}

.items {
    position: relative;
    perspective: 600px;
    width: 100%;
    height: 80%;
}

.albumInfo {
    width: 100%;
    height: 20%;
    display: flex;
    flex-direction: column;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 0.5px;
    position: absolute;
    left: 50%;
    top: 90%;
    transform: translate(-50%, -90%);
    z-index: -1;
}